label {
  font-weight: 700;
}

input {
  height: 36px;
  width: 100px;
  text-align: center;
  border: 1.5px solid #d3d3d3;
  border-radius: 2px;
  margin-left: 10px;
  margin-right: 25px;
  transition: box-shadow .2s ease-out;
}

input:hover {
  box-shadow: var(--shadow-default);
}

input:focus {
  box-shadow: var(--shadow-hover);
  outline-width: 0;
}

